import m from 'mithril';
import pics from "../../assets/img/*/*.jpg";
import { pictureAnimation, columnsExitAnim } from './Works'
import { cl } from '../utils';

const LeftArrow = {
  view(v) {
    const cn = v.attrs.disabled ? '[disabled]' : ''

    return m('.left-arrow',
      m(`button${cn}`, {
        onclick() {
          v.attrs.parentState.picIndex -= 1
        }
      }, 'prev')
    )
  }
}

const RightArrow = {
  view(v) {
    const cn = v.attrs.disabled ? '[disabled]' : ''

    return m('.right-arrow',
      m(`button${cn}`, {
        onclick() {
          v.attrs.parentState.picIndex += 1
        }
      },
        // m("svg.svg-icon[viewBox='0 0 20 20']",
        //   m("path[fill='currentColor'][d='M14.989,9.491L6.071,0.537C5.78,0.246,5.308,0.244,5.017,0.535c-0.294,0.29-0.294,0.763-0.003,1.054l8.394,8.428L5.014,18.41c-0.291,0.291-0.291,0.763,0,1.054c0.146,0.146,0.335,0.218,0.527,0.218c0.19,0,0.382-0.073,0.527-0.218l8.918-8.919C15.277,10.254,15.277,9.784,14.989,9.491z']")
        // )
        'next')
    )
  }
}
const handleClose = (v) => {
  v.attrs.parentState.picIndex = 0
  v.attrs.showModal = false
  const root = document.querySelector('.selected_root')
  cl('.overlay, .controls', 'add', 'hidden')
  cl('.selected_root', 'add', 'hidden')
  root.scrollTo({ top: 0, behavior: 'smooth' })

  columnsExitAnim.reverse()
  columnsExitAnim.play()
  pictureAnimation.reverse()
  pictureAnimation.complete = () => {
    cl('.modal', 'add', 'hidden')
    cl('.nav', 'remove', 'hidden')
    cl('.c-scrollbar', 'remove', 'd-none')
    cl('.column-item.v-hidden', 'remove', 'v-hidden')
    window.scroller.start()

  }
  pictureAnimation.play()

}

const CloseButton = {
  oncreate(v) {
    window.addEventListener('resize', () => handleClose(v))
  },
  view(v) {
    return m('.close-button',
      m('button.flex', {
        onclick() { handleClose(v) }
      },
        m("svg[viewBox='0 0 50 9']", {
          style: {
            stroke: 'white',
            fill: 'white',
            width: '5rem',
            height: '5rem',
            color: '#282829'
          }
        },
          m("path[stroke=currentColor][fill=currentColor][d='m0 4.5 5-3m-5 3 5 3m45-3h-77']")
        ),
        // m("svg.svg-icon[viewBox='0 0 20 20']", {
        //   style: {
        //     stroke: 'white',
        //     fill: 'white',
        //     width: '3rem',
        //     height: '3rem',
        //   }
        // },
        //   m("path[stroke=none][fill=#282828][d='M15.898,4.045c-0.271-0.272-0.713-0.272-0.986,0l-4.71,4.711L5.493,4.045c-0.272-0.272-0.714-0.272-0.986,0s-0.272,0.714,0,0.986l4.709,4.711l-4.71,4.711c-0.272,0.271-0.272,0.713,0,0.986c0.136,0.136,0.314,0.203,0.492,0.203c0.179,0,0.357-0.067,0.493-0.203l4.711-4.711l4.71,4.711c0.137,0.136,0.314,0.203,0.494,0.203c0.178,0,0.355-0.067,0.492-0.203c0.273-0.273,0.273-0.715,0-0.986l-4.711-4.711l4.711-4.711C16.172,4.759,16.172,4.317,15.898,4.045z']")
        // )
      ))
  }
}

function Modal() {
  return {
    oninit(v) {
      v.state.picIndex = 0
      v.state.selTitle = v.attrs.selTitle
      v.state.picsObj = pics[v.state.selTitle]
    },
    oncreate(v) {
      const root = document.querySelector('.selected_root')
      root.addEventListener('swiped-left', (_) => {
        if (v.state.picIndex < v.state.picsArray.length - 1) v.state.picIndex += 1
        m.redraw()
      })
      root.addEventListener('swiped-right', (_) => {
        if (v.state.picIndex > 0) v.state.picIndex -= 1
        m.redraw()
      })
    },
    view(v) {
      v.state.picsObj = pics[v.attrs.selTitle] || {}
      v.state.picsArray = Object.values(v.state.picsObj) || []
      return m('.modal.hidden[data-scroll][data-scroll-sticky][data-scroll-target=.main]',
        m('.selected_root',
          m('.carousel-wrap',
            m('.carousel',
              v.state.picsArray
                .map((url, i) => {
                  const clname = i === v.state.picIndex ? 'selected' : ''
                  const diff = v.state.picIndex - i
                  return m(`.img-wrap.${clname}[data-index=${i}]`, {
                    style: {
                      transform: `translate3d(${-diff * 100}%, ${0}px,  ${0}px)`,
                    },
                    key: url
                  },
                    m('img', { src: url })
                  )
                })
            ),
            m('.controls.hidden',
              m('.controls__bot',
                m('.space-between',
                  m(LeftArrow, { parentState: v.state, disabled: v.state.picIndex == 0 }),
                  m(RightArrow, { parentState: v.state, disabled: v.state.picIndex == v.state.picsArray.length - 1 }),
                ),
                m(`.controls__thumbs`, m('.thumbs-wrapper', {
                  style: {
                    transform: `translateX(-${v.state.picIndex * 20}%)`
                  }
                }, v.state.picsArray
                  .map((url, i) => m(`.thumb-wrap[data-index=${i}]${i == v.state.picIndex ? '.selected' : ''}`, {
                    key: url,
                    onclick() {
                      v.state.picIndex = i
                      const sx = 46 * i
                    }
                  },
                    m('img.placeholder', {
                      src: url,
                    })
                  )
                  ))
                ),
              ),
              m('.controls__top',
                m(CloseButton, { parentState: v.state, showModal: v.attrs.showModal }),
                m('div', v.attrs.selTitle)
              ),
              // m('.controls__left', v.state.picsArray
              //   .map((url, i) => m(`.thumb-wrap[data-index=${i}]${i == v.state.picIndex ? '.selected' : ''}`, {
              //     key: i,
              //     onclick() {
              //       v.state.picIndex = i
              //     }
              //   },
              //     m('img.placeholder', {
              //       src: url,
              //       onload(e) {
              //         cl(e.srcElement, 'add', 'loaded')
              //       },

              //     })
              //   )
              //   )

              // )

            ),
          ),

        ),

      )
    }
  }
}

export { SelectedPic, Modal }
